import store from '../store'
import API_ROUTE from '../constants/api_route'
import { change } from './Register'
//
const axios = require("axios")

export const fill = (option) =>{
    return {
        type: 'FILL',
        payload: option
    }
}
// send the requesto to backend
export const send = () =>{
    let state = store.getState().Register
    //console.log("state...", state,"email",state.email)
    return(dispatch) => {
        //console.log("entry in return")
        axios.post(API_ROUTE, {
            query: `query{
                userByEmail(email:"${state.email}"){ rol nameE }
            }`
        })
        .then((result)=>{
            let user = result.data.data.userByEmail
            dispatch(fill(['rol',user.rol]))
            dispatch(change(['nombre',user.nameE]))
        })
        .catch(err => console.log(err))
        
        console.log("need auth",state.email,state.password)

        // authentication
        axios.post(API_ROUTE,{
            query: `mutation{
                authUser(credentials: { email: "${state.email}", passw: "${state.password}"})
                { token success }
            }`    
        }).then((result)=>{
            
            const loginUser = result.data.data.authUser
            console.log("loginUSer: ",loginUser)
            dispatch(fill(['token',loginUser.token]))
            if(loginUser.success){
                //auth user incorrect
                dispatch(change(['authSuccess',true]))
            }
            
        }).catch(err => console.log(err))
    } 
}

// load all users
export const load = () =>{
    console.log('load users in login')
    return(dispatch)=> {
        axios.post(API_ROUTE, {
            query: `query{
                getUsers{
                    id
                    nameE
                    email
                    nit
                    rol
                }
            }`
            
        }).then((result)=>{
            let users  = result.data.data.getUsers
            dispatch(fill(['users',users]))
        }).catch(err => console.log(err))
    } 
    
}