import React, { Component } from 'react'
import '../css/services.css';

import AddIcon from '@material-ui/icons/Add';
//import auth from '../router/Auth'
import CardService from '../components/CardServices'
//import CommentIcon from '@material-ui/icons/Comment';
import { CssBaseline, Grid} from '@material-ui/core';
import Container from '@material-ui/core/Container';
import Dialog from '../components/DialogService'
import DialogN from '../components/DialogNotification'
import Fab from '@material-ui/core/Fab';
//import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
//import MyTheme from '../constants/MyTheme';
import Navbar from '../components/NavUser';
import SnackUser from '../components/SnackUser'
//import {ThemeProvider} from '@material-ui/core/styles';

//import store from '../store'
import Tooltip from '@material-ui/core/Tooltip';

import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux'
//import {load,open,openDN} from '../actions/Dialog'
import * as actions from '../_actions/Dialog';



//var wtf8 = require('wtf-8');

class Services extends Component{
    constructor(props){
        super(props)
        this.state = {
            
        }
        this.openDialog = this.openDialog.bind(this)
        // notification dialog
        this.openDialogN = this.openDialogN.bind(this)
    }

    openDialog(){ this.props.open('dialog')}
    openDialogN(){ this.props.openDN('dialog')}

    componentDidMount(){ 
        if (localStorage.getItem('view') === 'user'){
            this.props.history.push('/forbidden')
        }
        this.props.load('load data')
    }

    render(){
        
        const {dialog} = this.props
        //const {Login} = this.props
        const {register} = this.props

        //console.log("dialog in services: ",dialog['cards'])
        //console.log("login; ",Login)
        let cards = dialog['cards'].map(card =>{
            return(
                <CardService key={card.id} card={card}/>
            )
        })
        /*let a = store.getState().Dialog
        console.log(a['cards'])*/
        
        return(
            <div className="root">
                <SnackUser message={register.nombre}/>
                <Grid container component="main" className="pater" direction="row">
                    <Grid item xs={12}>
                        <Navbar/>
                        <CssBaseline/>
                    </Grid>
                    <Grid item xs={12} sm={1} md={1}>
                        <Container>
                            <Grid container direction="row" >
                                <Grid item xs={6} sm={12} className="fab">
                                    <Tooltip title="Crear Servicio">
                                        <Fab color="secondary" size="medium"  >
                                            <AddIcon onClick={this.openDialog} style={{ fontSize: 30 }}/>
                                        </Fab>
                                    </Tooltip>
                                    
                                </Grid>
                            </Grid>
                        </Container>
                    </Grid>
                    <Grid container  xs={12} sm={11} md={11} alignItems="center"  item={true} spacing={2} className="cards">
                            {cards}
                        <Container>
                            <Dialog></Dialog>
                            <DialogN></DialogN>    
                        </Container>
                    </Grid>
                </Grid>
                
                
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       dialog: state.Dialog,
       Login : state.Login,
       register: state.Register
    };
};
/*
const mapDispatchToProps = (dispatch) => {
    return{
       open: (date) => {dispatch(open(date))},
       openDN: (date) => {dispatch(openDN(date))},
       //load: (date) => {dispatch(load(date))}
    }
}*/
export default withRouter(connect(mapStateToProps,/*mapDispatchToProps,*/actions)(Services));