import React, { Component } from 'react'
import '../css/cardServices.css';


import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';

import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import EventIcon from '@material-ui/icons/Event';
import Fab from '@material-ui/core/Fab';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid' 

import MoreVertIcon from '@material-ui/icons/MoreVert';
import PlaceIcon from '@material-ui/icons/Place';
import Train from './Train'
import TrainIcon from '@material-ui/icons/Train';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';


import {connect} from 'react-redux'
// open Train 
import {opent} from '../_actions/Train';

var wtf8 = require('wtf-8');

class CardServices extends Component {
    constructor(props){
        super(props);  
        this.state = {
           dialogVerMas: false,
           dialogReservar: false,
           openT: false,
           open: false,
           id: 0
        }
        this.openTrain = this.openTrain.bind(this);
        /*this.handleClose = this.handleClose.bind(this);*/
          
    }
    openTrain(){this.props.opent('open train')}
    render(){
        
        return(
            <Grid item xs={12} sm={12} md={6}>
                <Card raised={true} className="card">
                    <CardHeader
                        avatar={<div className="avatar">R</div>}
                        title= {wtf8.decode(this.props.card.title)}
                        subheader= {this.props.card.class+"  &  "+this.props.card.transport}
                        action={<IconButton aria-label="settings"><MoreVertIcon /></IconButton>} 
                    />
                    
                    <CardContent>
                        <Grid container direction="row" className="body">
                            <Grid item xs={6} md={6} align="center" >
                                <PlaceIcon color={'primary'}/>
                                <h2>Lugar de origen</h2>
                                <h5>{wtf8.decode(this.props.card.origin)}</h5>
                            </Grid>
                            <Grid item xs={6} md={6} align="center" >
                                <PlaceIcon color={'secondary'}/>
                                <h2>Lugar de destino</h2>
                                <h5>{wtf8.decode(this.props.card.destination)}</h5>
                            </Grid>

                            <Grid item xs={6} md={6} align="center" >
                                <EventIcon color={'secondary'}/>
                                <h2>Fecha de salida</h2>
                                {this.props.card.date_start.slice(0,21)}
                            </Grid>
                            <Grid item xs={6} md={6} align="center" >
                            <EventIcon color={'primary'}/>
                                <h2>Fecha de llegada</h2>
                                {this.props.card.date_end.slice(0,21)}
                            </Grid>
                        </Grid>
                    </CardContent>
                    <CardActions>
                        <Grid item xs={2} sm={2} align="center">
                            {this.props.card.transport === 'terrestre' ? 
                                <br/> : 
                                <Fab color="secondary" size={'small'}>
                                    <TrainIcon onClick={this.openTrain}/>
                                </Fab>
                            }
                            <Train free={this.props.card.places} capacity={this.props.card.capacity} name={this.props.card.title}/>
                        </Grid>
                        <Grid item xs={4} sm={6} align="center"></Grid>
                        <Grid item xs={3} sm={2} align="center">
                            <Fab color="primary" size={'small'} > <EditIcon onClick={this.handleClose}/> </Fab>
                        </Grid>
                        <Grid item xs={3} sm={2} align="center">
                            <Fab color="secondary" size={'small'}><VisibilityOffIcon  onClick={this.handleClickButton2}/></Fab>
                        </Grid>
                    </CardActions>
                </Card>
            </Grid>
            
        )
    }
}

const mapStateToProps = (state) => {
    return {
       Train: state.Train
    };
};
const mapDispatchToProps = (dispatch) => {
    return{
        opent: (option) => {dispatch(opent(option))},
        /*change: (option) => {dispatch(change(option))},
       changeName: (option) => {dispatch(changeName(option))}*/
    }
}


export default (connect(mapStateToProps, mapDispatchToProps)(CardServices));