import React, { Component } from 'react'
import '../css/user.css';

//import { CssBaseline, Grid} from '@material-ui/core';
import AirplanemodeActiveIcon from '@material-ui/icons/AirplanemodeActive';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import DirectionsBoatIcon from '@material-ui/icons/DirectionsBoat';
import DirectionsBusIcon from '@material-ui/icons/DirectionsBus';
import Divider from '@material-ui/core/Divider';
import Fab from '@material-ui/core/Fab';
import {Grid} from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton'
import ImportExportIcon from '@material-ui/icons/ImportExport';
import ListCities from '../components/ListCities'
//import SearchIcon from '@material-ui/icons/Search';
import TrainIcon from '@material-ui/icons/Train';


import {connect} from 'react-redux'
import {open_drawer} from '../_actions/Drawer'
import {change} from '../_actions/Dialog'

//import Navbar from '../components/NavUser'


class DrawerUser extends Component {
    constructor(props) {
        super(props)
        this.state = {
            
        }
        this.handleClose = this.handleClose.bind(this);
    }

    handleClose(){
        this.props.open('drawer')
    }
    changeValues(){
        let origin = this.props.dialog.origin
        let destination = this.props.dialog.destination
        this.props.change(["Origen",destination]);
        this.props.change(["Destino",origin]);
        //console.log( this.props.dialog.origin,this.props.dialog.destination)
    }

    render(){
        const {dialog} = this.props
        return(
            <div>
                <Grid container direction="row" >
                    <Grid item xs={3} className="drawerHeader">
                        <IconButton className="button" onClick={this.handleDrawerOpen}>
                            <TrainIcon color={'secondary'}/>
                        </IconButton>
                    </Grid>
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton  className="button" onClick={this.handleDrawerOpen}>
                            <DirectionsBoatIcon color={'secondary'}/>
                        </IconButton>
                    </Grid>
                    
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton className="button" onClick={this.handleDrawerOpen}>
                            <AirplanemodeActiveIcon color={'secondary'}/>
                        </IconButton>
                    </Grid>
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton className="button" onClick={this.handleDrawerOpen}>
                            <DirectionsBusIcon color={'secondary'}/>
                        </IconButton>
                    </Grid>
                </Grid>
                <Grid container justify="center" alignItems="center" className='drawerH'>
                    <Grid item xs={1} md={1}></Grid>
                    <Grid item xs={9} md={9} >
                        <ListCities type="Origen" color="#6fc9b2"/>
                    </Grid>
                    <Grid item xs={2} md={2}>
                        <IconButton className='button' onClick={this.changeValues.bind(this)}>
                            <ImportExportIcon color={'secondary'}/> 
                        </IconButton>
                    </Grid>

                    <Grid item xs={1} md={1}></Grid>
                    <Grid item xs={9} md={9} >
                        <ListCities type="Destino" color="#6fc9b2"/>
                    </Grid>
                    <Grid item xs={2} md={2}>
                    </Grid>

                    <Grid item xs={10} md={10}></Grid>
                    <Grid item xs={2} md={2}>
                        <Fab color="secondary" size={'small'}><ChevronLeftIcon onClick={this.handleClose}/></Fab>
                    </Grid>
                    
                </Grid>
                <Divider/>
                <Grid item md={12}>
                    <div className="info_drawer">
                        {dialog.origin + " > " + dialog.destination}
                    </div>
                    {this.props.cards}
                </Grid>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        Drawer: state.Drawer,
        dialog: state.Dialog
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       open: (text) => {dispatch(open_drawer(text))},
       change: (value) => {dispatch(change(value))},
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(DrawerUser);