import API_ROUTE from '../constants/api_route'
import {load} from './Dialog';
const axios = require("axios")

export const open_drawer = (option) =>{
    return {
        type: 'OPEN_DRAWER',
        payload: option
    }
}

// change any value in dialog
export const change = (option) =>{
    return {
        type: 'CHANGE',
        payload: option
    }
}


// load alternatives 
export const loadA = () =>{
    console.log('load alternatives')
    return(dispatch)=> {
        axios.post(API_ROUTE, {
            query: `query{
                getAlternatives{
                    id
                    array_id
                    cost
                    array_services
                    duration
                    origin
                    destination
                }
            }`
            
        }).then((result)=>{
            let alternative  = result.data.data.getAlternatives
            dispatch(change(['alternatives',alternative]))
            dispatch(load())
        }).catch(err => console.log(err))
    } 
    
}