const defaultState = {
    open: false,
    id_user: 0,
    id_owner: 0,
    id_service: 0,
    requests: [],
    title: '',
    body: ''
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        
        case 'OPENR':
            return {...state,open:!state.open}
        case 'CHANGER':
            switch(payload[0]){
                case 'title':
                    return{...state,title:payload[1]}
                case 'body':
                    return{...state,body:payload[1]}
                case 'requests':
                    return{...state,requests:payload[1]}
                default:
                    return state
            }    
        default:
            return state
    }
    //return state
}

export default reducer;