class Auth{
    constructor(){
        this.auth = false
    }

    login(cb){
        console.log("entry in Auth.login")
        this.auth = true
        
        cb();
    }
    logout(cb){
        console.log("entry in Auth.logout")
        this.auth = false
        cb();
    }
    
    isAuth(){return this.auth}
}

export default new Auth();