import React,{Component}   from 'react';


import AttachFileIcon from '@material-ui/icons/AttachFile';
import Button from '@material-ui/core/Button';
import DateTime from './DateTime'
import Fab from '@material-ui/core/Fab';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Grid from '@material-ui/core/Grid';
import ListCities from './ListCities'
import ListTrans from './ListTrans'
import Popover from '@material-ui/core/Popover';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Switch from '@material-ui/core/Switch';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography'; 
//import {withRouter } from 'react-router-dom';

import '../css/stepper_service.css';
 
class StepService extends Component{
    
    constructor(props) {
        super(props)
        this.state = {
            activeStep: 0,
            setActiveStep: 0,
            skipped: new Set(),
            setSkipped: new Set(),
            steps: ['Descripción', 'Programación'],
            checked: true
        }
        /*const [activeStep, setActiveStep] = React.useState(0);
        const [skipped, setSkipped] = React.useState(new Set());
        const steps = getSteps();*/
        this.isStepSkipped = this.isStepSkipped.bind(this);
        this.isStepOptional = this.isStepOptional.bind(this);
        this.handleNext = this.handleNext.bind(this);
        this.handleBack = this.handleBack.bind(this);
        
    }
    
    getStepContent(step) {
        switch (step) {
            case 0:
                const id = this.state.open ? 'simple-popover' : undefined;
                return (
                    <div>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="title"
                            label="Titulo"
                            autoFocus
                            onChange={this.changeState}
                        />
                        <ListTrans type={"Transporte"}/>
                        <Grid container direction={'row'}>
                            <Grid item xs={10}>
                                <TextField
                                    variant="outlined"
                                    margin="normal"
                                    disabled={false}
                                    fullWidth
                                    id="capacity"
                                    label="Capacidad"
                                    autoFocus
                                    onChange={this.changeState}
                                />
                            </Grid>
                            <Grid item xs={2} align={'center'} className="text_info">
                                <Fab color="secondary" size={'small'}>
                                    <AttachFileIcon onClick={this.openPop}/>
                                    <Popover
                                        id={id}
                                        open={this.state.openP}
                                        anchorEl={this.state.anchorEl}
                                        onClose={this.openPop}
                                        anchorOrigin={{
                                            vertical: 'bottom',
                                            horizontal: 'center',
                                        }}
                                        transformOrigin={{
                                            vertical: 'top',
                                            horizontal: 'center',
                                        }}
                                    >
                                        <Typography className="typography">
                                        <b>Capacidad: </b>Plataformas que posee el tren 
                                        </Typography>
                                        <Typography className="typography">
                                        <b>Espacios: </b>Plataformas disponibles del tren
                                        </Typography>
                                    </Popover>
                                </Fab>
                            </Grid>
                        </Grid>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            id="places"
                            label="Espacios"
                            autoFocus
                            onChange={this.changeState}
                        />
                        <TextField
                            variant="outlined"
                            margin="normal"
                            //required
                            fullWidth
                            id="cost"
                            label="Costo"
                            name="costo"
                            autoFocus
                            onChange={this.changeState}
                        />
                    </div>
                )
            case 1:
                return (
                    <div>
                        <Grid container direction="row">
                            <Grid item xs={6}>
                                <ListCities type={"Origen"} color="#132947"/>
                            </Grid>
                            <Grid item xs={6}>
                                <ListCities type={"Destino"} color="#132947"/>
                            </Grid>
                        </Grid>
                        <Grid container direction={'row'}>
                            <Grid item xs={6}>
                                <DateTime labelDate={"Fecha Salida"} labelTime={"Hora Salida"}/>
                            </Grid>
                            <Grid item xs={6}>
                                <DateTime labelDate={"Fecha Llegada"} labelTime={"Hora Llegada"}/>        
                            </Grid>
                        </Grid>

                        <Grid container direction={'row'} className="switch">
                            <Grid item xs={8} align="center">
                                Permite que otros usuarios vean el servicio
                            </Grid>
                            <Grid item xs={4} align="center">
                                <FormControlLabel
                                    control={
                                    <Switch
                                        checked={this.state.checked}
                                        onChange={this.handleChange.bind(this)}
                                        value="checked"
                                        color="primary"
                                        inputProps={{ 'aria-label': 'primary checkbox' }}
                                    />
                                    }
                                    label="Hacer visible"
                                />
                            </Grid>
                        </Grid>
                        
                    </div>
                )
            default:
                return 'Unknown step';
        }
    }
    
    //change que switch state
    handleChange(){
        this.setState({
            checked: !this.state.checked
        })
    }
    isStepOptional = step => {
        return step === 1;
    };

    isStepSkipped = step => {
        return this.state.skipped.has(step);
    };

    async handleNext() {
        await this.setState({activeStep: this.state.activeStep + 1})
    };

    async handleBack () {
        await this.setState({activeStep: this.state.activeStep - 1})
        //console.log("hallo: ",this.state.activeStep)
    };

    handleSkip = () => {
        if (!this.isStepOptional(this.state.activeStep)) {
        // You probably want to guard against something like this,
        // it should never occur unless someone's actively trying to break something.
            throw new Error("You can't skip a step that isn't optional.");
        }

        //this.state.setActiveStep(prevActiveStep => prevActiveStep + 1);
        this.setState({
            setActiveStep: prevActiveStep => prevActiveStep + 1
        })
        this.setState({
            setSkipped: prevSkipped => {
                const newSkipped = new Set(prevSkipped.values());
                newSkipped.add(this.state.activeStep);
                return newSkipped;
            }
        })
        /*this.state.setSkipped(prevSkipped => {
            const newSkipped = new Set(prevSkipped.values());
            newSkipped.add(this.state.activeStep);
            return newSkipped;
        });*/
    };

    handleReset = () => {
        this.setState({activeStep: 0})
    };
 
    
    render(){
        
        return(
            <div className="root">
                <Grid container direction="row">
                    <Grid item xs={12} sm={12} md={12}>
                        <Stepper activeStep={this.state.activeStep}>
                            {   
                                this.state.steps.map((label, index) => {
                                    const stepProps = {};
                                    const labelProps = {};
                                    if (this.isStepSkipped(index)) {
                                        stepProps.completed = false;
                                    }
                                    return (
                                        <Step key={label} {...stepProps}>
                                            <StepLabel {...labelProps}>{label}</StepLabel>
                                        </Step>
                                    );
                                })
                            }
                        </Stepper>
                    </Grid>
                
                    <Grid item xs={12} sm={12} md={12}>
                        <div>
                            {this.state.activeStep === this.state.steps.length ? (
                                <div>
                                    <Grid item xs={12} align="center">
                                    <Typography className="instructions">
                                        Servicio configurado correctamente.
                                    </Typography>
                                    </Grid>

                                    <Grid container direction="row" className="buttons">
                                        <Grid item xs={4} sm={4} md={4} align="center">
                                            <Button 
                                                fullWidth
                                                variant="outlined"
                                                color="inherit"
                                                onClick={this.handleReset}
                                                className="button"
                                                >
                                                Reiniciar
                                            </Button>
                                        </Grid>
                                        <Grid item xs={4} sm={4} md={4}></Grid>
                                        <Grid item xs={4} sm={4} md={4} align="center">
                                            <Button
                                                fullWidth
                                                variant="contained"
                                                color="primary"
                                                //onClick={this.sendRequest}
                                                className="button"
                                                >
                                                Crear
                                            </Button>
                                        </Grid>
                                    </Grid>
                                    
                                </div>
                            ) : (
                                <div>
                                    <Typography className="instructions">{this.getStepContent(this.state.activeStep)}</Typography>
                                    <div>
                                        <Grid container direction="row" className="buttons">
                                            <Grid item xs={4} sm={4} md={4} align="center">
                                                <Button 
                                                    fullWidth
                                                    variant="contained"
                                                    color="secondary"
                                                    disabled={this.state.activeStep === 0} 
                                                    onClick={this.handleBack} 
                                                    className="button"
                                                    >
                                                    Atras
                                                </Button>
                                            </Grid>
                                            <Grid item xs={4} sm={4} md={4}></Grid>
                                            <Grid item xs={4} sm={4} md={4} align="center">
                                                <Button
                                                    fullWidth
                                                    variant="contained"
                                                    color="primary"
                                                    onClick={this.handleNext}
                                                    className="button"
                                                    >
                                                    {this.state.activeStep === this.state.steps.length - 1 ? 'Terminar' : 'Siguiente'}
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    </div>
                                </div>
                            )}
                        </div>
                    </Grid>
                </Grid>    
            </div>
        );
    }
}
 
export default StepService;