import React, { Component } from 'react'
import '../css/listInfo.css'

import Collapse from '@material-ui/core/Collapse';
import DirectionsBusIcon from '@material-ui/icons/DirectionsBus';
import DialogRequest from './DialogRequest'
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import Fab from '@material-ui/core/Fab';
import { Grid } from '@material-ui/core';
import GroupIcon from '@material-ui/icons/Group';
import LabelIcon from '@material-ui/icons/Label';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MessageIcon from '@material-ui/icons/Message';
import Train from './Train'
import TrainIcon from '@material-ui/icons/Train';

import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';
import { withRouter } from 'react-router-dom';
//import * as actions from '../actions/Login';
import {load} from '../_actions/Login';

// open request generated by user
import {openr} from '../_actions/Request';
// open Train 
import {opent} from '../_actions/Train';

// Redux
import {connect} from 'react-redux'


var wtf8 = require('wtf-8');



class ListInfo extends Component {
   
   constructor(props){
        super(props);
        this.state = {
            SetOpen: false,
            open: false,
            info:[
                {
                'id': 1,
                'nombre': 'Pepito Perez',
                'rol': 'Transportador',  
                'Servicio': 'otra cosa'
                },
                {
                'id': 2,
                'nombre': 'juan Perez',
                'rol': 'Transportador',  
                'Servicio': 'otra cosa x2'
                }
            ],
            capacity: 120,
            places: 10
            
         
        }
        this.handleClick = this.handleClick.bind(this)
        this.updateTrain = this.updateTrain.bind(this)
        this.OpenRequest = this.OpenRequest.bind(this)
        this.openTrain = this.openTrain.bind(this)
      
      
   }
   // Origin 
    handleClick(){
        localStorage.setItem('type', 'Origin');
        this.setState({
            setOpen: !this.state.open,
            open: !this.state.setOpen
        })
    }
    componentDidMount(){this.props.load('hallo')}
    openTrain(){this.props.opent('open train')}
    updateTrain(value){
        //console.log("value: ",value)
        //this.props.change(value)
    }


    /*componentWillMount() {
      //this.props.change([this.state.capacity,this.state.places])   
    }*/
    async OpenRequest(){
        //temṕoral arreglar luego
        this.props.openr('open request')
    }
   
    render(){
      
        const { dialog } = this.props;
        const { login } = this.props
        //console.log('login in listinfo:',login)

        let serv = []
        //console.log(dialog)
        dialog.cards.map(option => {
            serv.push([option.id,option.title,option.date_start ,option.date_end])
            return(<div></div>)
        })

        let info = login.users.map(option => {
            
            let id_user = parseInt(this.props.alternative.array_id.split(',')[option.id-1])
            let id_service = parseInt(this.props.alternative.array_services.split(',')[option.id-1])
        
            let any = dialog.cards.map(service =>{
                if (option.id === id_user && service.id === id_service){
                    //console.log("trans: ",service.transport)
                    return(
                        <div className="div-list">
                            <List component="div" disablePadding key={option.id}>
                                <ListItem selected={true} >
                                    <Grid container direction={"row"}>
                                        <Grid item xs={2} md={2}><GroupIcon color="primary"/></Grid>
                                        <Grid item xs={8} md={8}>
                                            {option.nameE}
                                        </Grid>
                                        <Grid item xs={2} md={2} align="right">
                                            <VerifiedUserIcon color="secondary"/>
                                        </Grid>
                                    </Grid>
                                    
                                </ListItem>

                                <ListItem>
                                    <Grid item xs={2} md={2}>
                                        {service.transport === 'terrestre' ? 
                                            <DirectionsBusIcon/> : 
                                            <Fab color="primary" size={'small'} align={"left"}>
                                                <TrainIcon onClick={this.openTrain}/>
                                            </Fab>
                                        }
                                        <Train free={service.places} capacity={service.capacity} name={service.title}/>
                                    </Grid>
                                    <Grid item xs={10} md={10}>{wtf8.decode(service.title)}</Grid>
                                </ListItem>

                                <ListItem>
                                    <Grid item xs={2} md={2}><LabelIcon color="primary"/></Grid>
                                    <Grid item xs={10} md={10}>{wtf8.decode(service.origin)}</Grid>
                                </ListItem>

                                <ListItem>
                                    <Grid item xs={2} md={2}><LabelIcon color="primary"/></Grid>
                                    <Grid item xs={10} md={10}>{service.date_start.slice(0,21)}</Grid>
                                </ListItem>

                                <ListItem>
                                    <Grid item xs={2} md={2}><LabelIcon color="primary"/></Grid>
                                    <Grid item xs={10} md={10}>{wtf8.decode(service.destination)}</Grid>
                                </ListItem>
                                
                                <ListItem>
                                    <Grid item xs={2} md={2}><LabelIcon color="primary"/></Grid>
                                    <Grid item xs={10} md={10}>{service.date_end.slice(0,21)}</Grid>
                                </ListItem>
                                
                                <ListItem>
                                    <Grid item xs={10} md={10}>Solicitar reserva</Grid>
                                    <Grid item xs={2} md={2} align="right">
                                        <Fab color="secondary" size={'small'} >
                                            <MessageIcon onClick={this.OpenRequest}/>
                                        </Fab>
                                    </Grid>
                                </ListItem>
                            </List>
                        </div>
                        
                    )
                }else{return(<p></p>)}
         })
         return any
      })
      //console.log("classe of list-->> ",listCities)
      //this.props.openList('hallo')
      return(
         <List justify="center" color={'black'}>
            <ListItem button onClick={this.handleClick}>
               <ListItemText primary={this.props.type}/>
               {this.state.open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={this.state.open} timeout="auto" unmountOnExit>
                {info}
               <DialogRequest/>
            </Collapse>
            
         </List>
         
      )
   }
}

const mapStateToProps = (state) => {
    return {
        dialog: state.Dialog,
        login: state.Login,
        request: state.Request
    };
};
const mapDispatchToProps = (dispatch) => {
    return{
        openr: (option) => {dispatch(openr(option))},
        opent: (option) => {dispatch(opent(option))},
        load: (option) => {dispatch(load(option))},
        // change: (option) => {dispatch(change(option))},
    }
}

export default withRouter((connect(mapStateToProps,mapDispatchToProps,/*actions*/)(ListInfo)));