import React, { Component } from 'react'
import '../css/dialog.css'; 

//import AttachFileIcon from '@material-ui/icons/AttachFile';
//import Button from '@material-ui/core/Button';
//import DateTime from './DateTime'
import Dialog from '@material-ui/core/Dialog';
//import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import Fab from '@material-ui/core/Fab';
import { Grid } from '@material-ui/core';

//import ListCities from './ListCities'
//import ListTrans from './ListTrans'
//import Popover from '@material-ui/core/Popover';

import StepService from '../components/StepService';
import store from '../store'
//import TextField from '@material-ui/core/TextField';


//import Typography from '@material-ui/core/Typography';


import {connect} from 'react-redux'
import {create,change,open} from '../_actions/Dialog'

class DialogService extends Component {
    constructor(props){
        super(props);
        this.state = {
            /*SetOpenO: false,
            openO: false,
            openD: false,
            setOpenD: false,
            openT: false,
            setOpenT: false,
            //popOver
            openP: false,
            anchorEl: null*/
            openP: false,
            anchorEl: null
        }
        
        //this.handleClick = this.handleClick.bind(this)
        //this.handleClose = this.handleClose.bind(this)
        this.SendRequest = this.SendRequest.bind(this);
        this.openPop = this.openPop.bind(this);
        this.changeState = this.changeState.bind(this);
        this.closeDialog = this.closeDialog.bind(this);
    }

    openPop(event){
        this.setState({ anchorEl: event.currentTarget, openP: !this.state.openP })
    }

    componentDidMount(){ 
        let id_user = store.getState().Login.id_user
        this.props.change(['id_user',id_user])
    }

    async changeState(event){
        const target = event.target.id;
        const value = event.target.value;
        this.props.change([target,value])
    }

    async SendRequest(){this.props.open('io')}
    closeDialog(){ this.props.open('dialog')}
    render(){
        
        const { dialog } = this.props;
        //console.log("dialog",dialog)

        //const id = this.state.open ? 'simple-popover' : undefined;
        return(
            <Dialog open={dialog.open}>
                
                <DialogTitle >
                    <Grid container direction="row">
                        <Grid item xs={5} md={5}>{"Crear Nuevo Servicio"}</Grid>
                        <Grid item xs={3} md={3}></Grid>
                        <Grid item xs={4} md={4} align="right">
                            <Fab color="primary" size="small" >
                                <ExitToAppIcon onClick={this.closeDialog}/>
                            </Fab>
                        </Grid>
                    </Grid>
                </DialogTitle>
                <DialogContent>
                    <StepService/>
                </DialogContent>
            </Dialog>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       dialog: state.Dialog,
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
       change: (date) => {dispatch(change(date))},
       open: (date) => {dispatch(open(date))},
       create: (text) => {dispatch(create(text))},
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(DialogService);