import React from 'react'
import Admin from '../views/Admin/Admin'

import Forbidden from '../views/Forbidden'
import Home from '../views/Home'
import Log_in from '../views/Log_in'

import { Route, Switch } from 'react-router-dom'
import Services from '../views/Services'
import Sign_up from '../views/Sign_up'
import { ProtectedRoute } from './ProtectedRoute'

import User from '../views/User'

export default props => (
    <Switch>
        <Route exact path="/" component={Home}/>
        <Route exact path="/sign_up" component={Sign_up}/>
        <Route exact path="/log_in" component={Log_in}/>
        <ProtectedRoute exact path="/forbidden" component={Forbidden}/>
        <ProtectedRoute exact path="/services" component={Services}/>
        <ProtectedRoute exact path="/user" component={User}/>
        <ProtectedRoute exact path="/admin_home" component={Admin}/>
    </Switch>
)