import React, { Component } from 'react'
import '../css/train.css';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid' 
// open Train 
import {opent} from '../_actions/Train';
import train from '../resources/Train.png';
import { withRouter } from 'react-router-dom';

//redux
import {connect} from 'react-redux'


class Train extends Component {
    
    constructor(props){
        super(props);
        this.state = {}
        this.openTrain = this.openTrain.bind(this)
    }
    openTrain(){this.props.opent('close train')}

    render(){
        
        const {Train} = this.props
        let title = 'Estado del Tren: '+ this.props.name
        return(
            
            <Dialog open={Train.opent}>
                
                <DialogTitle align={"center"}>
                    <div className="title"><b>{title}</b></div>
                    
                </DialogTitle>
                <DialogContent>
                    <Grid container direction="row" item={true} spacing={1} className="all">
                        <Grid item xs={12} sm={4} md={4} align="center">
                            <img src={train} alt="train" width="150" height="110"/>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center" >
                            <div className="TrainE">
                                <h2>{this.props.free}</h2>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center">
                            <div className="TrainF">
                                <h2>{parseInt(this.props.capacity)-parseInt(this.props.free)}</h2>
                            </div>
                        </Grid>

                        <Grid item xs={12} sm={8} md={8} align="center" className="text_info">
                            <p>
                            Contenedor <b>Verde</b> indica
                            las plataformas <i>libres del tren</i>
                            </p>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center" >
                            <div className="TrainSE">
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={8} md={8} align="center" className="text_info" >
                            <p>
                            Contenedor <b>Rojo</b> indica
                            las plataformas <i>ocupadas del tren</i>
                            </p>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center">
                            <div className="TrainSF">
                            </div>
                        </Grid>

                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button variant="contained" color="primary" onClick={this.openTrain}> cerrar </Button>
                </DialogActions>
            </Dialog>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       Train: state.Train
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        opent: (option) => {dispatch(opent(option))},
        
    }
}
export default withRouter((connect(mapStateToProps,mapDispatchToProps)(Train)));