import React,{Component}   from 'react';


import Avatar from '@material-ui/core/Avatar';
import BackDrop from '../components/BackDrop'
import Button from '@material-ui/core/Button';
import Chip from '@material-ui/core/Chip';
import DoneIcon from '@material-ui/icons/Done';
import Grid from '@material-ui/core/Grid';
//import TextField from '@material-ui/core/TextField';
import SnackAlert from '../components/_Snacks/SnackAlert';
import TextEmail from '../components/_Form/TextEmail'
import TextPassword from '../components/_Form/TextPassword'
import Typography from '@material-ui/core/Typography'; 
import { withRouter } from 'react-router-dom';


import {connect} from 'react-redux'
import {change,email_success} from '../_actions/Register'
// this action have to change into Register
import {send,fill} from '../_actions/Login'
// view loading
import {openBD} from '../_actions/actions_backdrop' 
import { openSnack } from '../_actions/actions_snack'

class StepLogin extends Component{
    constructor(props) {
        super(props)
        this.state = {
            activeStep: 0,
            skipped: new Set(),
            message_backdrop: '',
            message_snackAlert: ''
        }
        this.handleNext = this.handleNext.bind(this)
        this.handleReset = this.handleReset.bind(this)
        this.handleBack = this.handleBack.bind(this)
    }
    changeEmail(){
        this.setState({activeStep: this.state.activeStep-1})
        this.props.change(['validate_email',true])
    }
    handleNext() {
        let {Register} = this.props
        if (this.state.activeStep < 1){
            
            // verify email
            this.setState({message_backdrop: 'Verificando correo electrónico..'})
            this.props.openbd('back_drop')
            setTimeout(() => { 
                //this.props.openbd('back_drop')
                this.props.email_success(Register.email,'log_in');    
            }, 2000);
            
            // change View
            setTimeout(() => { 
                //console.log("before change view")
                let {Register} = this.props
                //console.log(Register.validate_email, Register.email, Register.unique)
                if (!Register.validate_email & Register.email.length > 0 & Register.unique){
                    //this.props.openbd('back_drop')
                    this.setState({activeStep: this.state.activeStep + 1})
                }else{
                    this.setState({ message_snackAlert: 'No se ha podido encontrar tu cuenta'})
                    this.props.open_snack('snack_alert');
                    setTimeout(() => { this.props.open_snack('snack_alert');}, 3000);
                }

                this.props.openbd('back_drop')
            }, 6000);
            
        }else{
            //send request to log_in
            this.setState({message_backdrop: 'Autenticando usuario..'})
            this.props.openbd('back_drop')
            if (Register.password.length > 0){
                setTimeout(() => { this.props.send('') }, 2000);
                setTimeout(() => { 
                    let {Register} = this.props;
                    let {Login} = this.props;
                    if (Register.authSuccess){
                        
                        //this.props.fill(['isAuth',true])
                        //setTimeout(() => {}, 1000);
                        localStorage.setItem('auth',true)
                        
                        if(Login.rol === 'generador'){
                            this.props.history.push('/user')
                            localStorage.setItem('view','user')
                        }
                        else{
                            this.props.history.push('/services')
                            localStorage.setItem('view','services')
                        }
                        
                    }
                    this.props.openbd('back_drop')
                }, 3000);
            }
            //alert('correo no existe')
        }
    };

    handleReset = () => { this.setState({activeStep: 0})};
    handleBack () { 
        
        if (this.state.activeStep === 0){
            this.props.history.push("/sign_up")

        }else{
            alert('recovery pass')
        }
    };


    isStepOptional = step => {
        return step === 1;
    };

    isStepSkipped = step => {
        return this.state.skipped.has(step);
    };
    
    
    getStepContent(step) {
        let {Register} = this.props
        switch (step) {
            case 0:
                //const id = this.state.open ? 'simple-popover' : undefined;
                return(
                    <div className="root_step" id="root_step">
                        <Grid container spacing={4} direction="row" justify="center">
                            <Grid item sm={8}>
                                <TextEmail label="Correo electrónico" id="email" view="log_in"/>
                            </Grid>
                        </Grid>
                    </div>
                )
            case 1:
                return (
                    <div>
                        <Grid container spacing={4} direction="row" justify="center" alignItems="center">
                            <Grid item sm={5} align="right">
                                <h2>Bienvenido</h2>
                            </Grid>
                            <Grid item sm={7}>
                                <Chip
                                    avatar={<Avatar>M</Avatar>}
                                    label={Register.email}
                                    clickable
                                    color="primary"
                                    onClick={this.changeEmail.bind(this)}
                                    deleteIcon={<DoneIcon />}
                                />
                            </Grid>
                            <Grid item sm={8}>
                                <TextPassword label="Ingresa tu contraseña" width={150} id="password"/>
                            </Grid>
                        </Grid>
                    </div>
                )
            default:
                return 'Unknown step';
        }
    }
    render(){
        return(
            <div className="root">
                <Grid container direction="row">
                    <Grid item xs={12} sm={12} md={12}>
                        <Typography className="instructions">
                            {this.getStepContent(this.state.activeStep)}
                        </Typography>

                        <BackDrop message={this.state.message_backdrop}/>
                        <SnackAlert message={this.state.message_snackAlert}/>

                        <Grid container direction="row" className="buttons" justify="center">
                            <Grid item xs={1} sm={1} md={1}></Grid>
                            <Grid item xs={4} sm={5} md={5}>
                                <Button 
                                    color="secondary"
                                    onClick={this.handleBack} 
                                    className="button"
                                    >
                                    {this.state.activeStep === 0 ? 'Crear Cuenta' : 'Olvidaste tu contraseña'}
                                </Button>
                            </Grid>
                            <Grid item xs={4} sm={1} md={1}></Grid>
                            <Grid item xs={4} sm={4} md={4} align="center">
                                <Button
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    onClick={this.handleNext}
                                    className="button"
                                    >
                                    Siguiente
                                </Button>
                            </Grid>
                            <Grid item xs={4} sm={1} md={1}></Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        Register: state.Register,
        Login: state.Login
    };
};
 
const mapDispatchToProps = (dispatch) => {
    return{
       change: (text) => {dispatch(change(text))},
       email_success: (text) => {dispatch(email_success(text))},
       fill: (text) => {dispatch(fill(text))},
       openbd: (text) => {dispatch(openBD(text))},
       open_snack: (text) => {dispatch(openSnack(text))},
       send: (text) => {dispatch(send(text))},
       
    }
}

 
export default  withRouter(connect(mapStateToProps,mapDispatchToProps)(StepLogin)) ;