export const defaultState = {
    anchorEl: null,
    open: false,
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'OPEN_MENU':
            //console.log("entry in dialog open")
            return{ ...state,open: !state.open}
        case 'CHANGE_MENU':
            switch(payload[0]){
                case 'anchorEl':{return {...state,anchorEl: payload[1]}}
                default: return state
            }
        default:
            return state
            
    }
    
}

export default reducer;