import API_ROUTE from '../constants/api_route'


export const defaultState = {
    open: false,
    cards: [],
    openN: false,
    id_user:  0,
    title: '',
    type: 'carga',
    capacity: 0,
    places: 0,
    cost: '',
    cities: [
        {name:'Bogotá' ,lat: 4.6097100, lng: -74.0817500 ,option: false},
        {name:'Santa Marta' ,lat: 11.2407900, lng: -74.1990400 ,option: false},
        {name:'Barranquilla' ,lat: 10.9685400, lng: -74.7813200 ,option: false},
        {name:'Cartagena' ,lat: 10.3997200, lng: -75.5144400 ,option: false},
        {name:'La dorada' ,lat: 5.4478302, lng: -74.6631088 ,option: false},
        {name:'Belencito' ,lat: 6.2518400, lng: -75.5635900 ,option: false},
    ],
    origin: '',
    destination: '',
    transport: '',
    ubication: '',
    dateStart: new Date('2019-10-31T09:00:00'),
    dateEnd: new Date('2019-10-31T09:00:00')
}

const reducer = (state = defaultState, { type, payload }) => {
    const axios = require("axios")
    switch(type){
        case 'OPEN':
            //console.log("entry in dialog open")
            return{
                ...state,open: !state.open
            }
        case 'OPENDN':
            return{...state,openN: !state.openN}
        case 'CHANGE':
            switch(payload[0]){
                case 'id_user':
                    return {...state,id_user:payload[1]}
                case 'cards':
                    return {...state,cards:payload[1]}
                case 'title':
                    return {...state,title:payload[1]}
                case 'capacity':
                    return {...state,capacity:parseInt(payload[1])}
                case 'places':
                    return {...state,places:parseInt(payload[1])}
                case 'cost':
                    return {...state,cost:payload[1]}
                case 'Transporte':
                    return {...state,transport:payload[1]}
                case 'Origen':
                    return{...state,origin:payload[1]}
                case 'Destino':
                    return{...state,destination:payload[1]}
                case 'salida':
                    return{...state, dateStart: payload[1]}
                default:
                    return{...state,dateEnd: payload[1]}
            }
        case'CREATE':
            
            //let transport = store.getState().ListTrans.
            axios.post(API_ROUTE,{
                query : `mutation{
                    createService(Service: {
                        id_user: ${state.id_user}
                        title:"${state.title}"
                        class:"${state.type}"
                        capacity: ${state.capacity}
                        places: ${state.places}
                        cost: "${state.cost}"
                        origin :"${state.origin}"
                        destination :"${state.destination}"
                        transport :"${state.transport}"
                        ubication:"${state.ubication}"
                        date_start:"${state.dateStart}"
                        date_end:"${state.dateEnd}"
                    }){
                        id
                        title
                    }
                }`
            }).then((result)=>{
             
                //console.log(result.data.data.createService)
                const title = result.data.data.createService.title
                //const id = result.data.data.createService.id
                if(title !== " "){
                    //console.log("service created")
                }else{
                    alert("No se creo el servicio")
                }
            }).catch(err => console.log(err))

            //console.log("cities from dialog SendRq: ",payload)
            break;
        default:
            return state
            
    }
    
}

export default reducer;