import API_ROUTE from '../constants/api_route'
const axios = require("axios")

export const openr = (option) =>{
    return {
        type: 'OPENR',
        payload: option
    }
}

//change the request values
export const changer = (option) =>{
    return {
        type: 'CHANGER',
        payload: option
    }
}


