import React, { Component } from 'react'

import FormControlLabel from '@material-ui/core/FormControlLabel';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';

//import InboxIcon from '@material-ui/icons/MoveToInbox';
import PersonIcon from '@material-ui/icons/Person';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Radio from '@material-ui/core/Radio';
import { RadioGroup } from '@material-ui/core';


// Redux
import {connect} from 'react-redux'
//import {change} from '../actions/ListRol'
import {change} from '../_actions/Register'
import {open} from '../_actions/ListRol'


class ListRol extends Component {
    constructor(props) {
        super(props)
        this.state = {
            text: ''
        }
        this.changeState = this.changeState.bind(this);
        this.OpenList = this.OpenList.bind(this);
    }
    async changeState(event){
        let option = event.target.value
        this.props.change(['rol',option]);
        this.props.change(['validate_rol',false]);
        this.setState({ text: option })
        
    }
    
    OpenList() {this.props.open('change')};

    render() {
        let {listRol} = this.props
        return (
            <div>
                <List justify="center">
                    <ListItem button onClick={this.OpenList}>
                        <ListItemIcon>
                            <PersonIcon color="primary" />    
                        </ListItemIcon>
                        <ListItemText primary={
                            this.state.text === '' ? 
                            "Rol" : 
                            "Rol" +" : "+this.state.text
                        }
                        />
                        {listRol.open ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    
                    <Collapse in={listRol.open} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            <RadioGroup >
                                <ListItem className="nested">
                                <FormControlLabel
                                    control={<Radio onChange={this.changeState} value="generador"/>}
                                    label="Generador de Carga"
                                />
                                </ListItem>
                                <ListItem className="nested">
                                <FormControlLabel
                                    control={<Radio disabled={false} onChange={this.changeState} value="transportador"/>}
                                    label="Transportador de Carga"
                                />
                                </ListItem>
                                <ListItem className="nested">
                                <FormControlLabel
                                    control={<Radio disabled={true} onChange={this.changeState} value="operador"/>}
                                    label="Operador logistico"
                                />
                                </ListItem>
                            </RadioGroup>
                        </List>
                    </Collapse>
                </List>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       listRol: state.ListRol,
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       change: (text) => {dispatch(change(text))},
       open: (text) => {dispatch(open(text))}
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(ListRol);