import React, { Component } from 'react'
import '../css/login.css';

import auth from '../router/Auth'
//import BackDrop from '../components/BackDrop'
//import Button from '@material-ui/core/Button';
//import Checkbox from '@material-ui/core/Checkbox';
import CssBaseline from '@material-ui/core/CssBaseline';
import Fab from '@material-ui/core/Fab';
//import FormControlLabel from '@material-ui/core/FormControlLabel';
import Grid from '@material-ui/core/Grid';
//import HomeIcon from '@material-ui/icons/Home';
import LinkUI from '@material-ui/core/Link';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import StepLogin from '../components/StepLogin'
//import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import { withRouter } from 'react-router-dom';

// Redux
import {connect} from 'react-redux'
import {fill,send} from '../_actions/Login'


class Log_in extends Component {
    constructor(props) {
        super(props)
        //this.state = {}
        this.inputChange = this.inputChange.bind(this);
        this.sendRequest = this.sendRequest.bind(this);
        this.changeView = this.changeView.bind(this);
        this.moveRegister = this.moveRegister.bind(this);
    }

    async inputChange(event){
        let {id , value} = event.target
        var md5 = require('md5');
        if (id === 'password'){value = md5(value)}
        this.props.fill([id,value])
    }
    
    
    changeView(rol){
        
        if(rol === 'generador'){
            auth.login(() =>{this.props.history.push("/user")}) 
            //localStorage.setItem('auth',true)
            //this.props.history.push("/user")
        }
            
        if(rol === 'transportador'){
            auth.login(() =>{this.props.history.push("/services")}) 
            //localStorage.setItem('auth',true)
            //this.props.history.push("/services")
        }
    }
    
    async sendRequest(){ 
        this.props.send();
        /*setTimeout(function(){
            login = store.getState().Login
            
        },2000);*/
        
        //await this.changeView();
        
        /*const { Login } = this.props;
        console.log("rol before push in login",Login.rol)
        if(Login.rol === 'generador'){this.props.history.push("/user")}
        else{this.props.history.push("/services")}
        let promise = new Promise(function(resolve,reject){
            setTimeout(function(){
                resolve(
                    "hallo promise"        
                );
            },2000);
        })
        //const { Login } = this.props;
        
        promise.then(function(response){
            let login = store.getState().Login
            console.log("rol before push in login",login.rol)
            return '12'
        })*/
    }
    
    moveRegister(){
        this.props.history.push("/sign_up")
    }
    render(){
        
        return(
            <Grid container item spacing={4} component="main" className="root" >
                <CssBaseline/>

                <Grid item xs={12} sm={6} md={6} component={Paper} elevation={6} square>
                    <div className="paper">
                        <div className="Avatar_login">
                            <Fab color="primary" size={'large'} >
                                <LockOutlinedIcon />
                            </Fab>
                        </div>
                        <div className="title">
                            <Typography component="h1" variant="h5" > Inicio de Sesión</Typography>
                        </div>
                        <div className="stepper_login">
                            <StepLogin />
                        </div>
                        <form className="form" noValidate>
                            
                            <Grid container item spacing={3} justify="center">
                                <Grid item xs={10} sm={10} > 
                                    <Typography variant="body2" color="textSecondary" align="center" className="footer_login">
                                        {'Intermodalidapp & '}
                                        <LinkUI color="inherit" href="https://material-ui.com/">
                                            Material-UI
                                        </LinkUI>
                                    </Typography>
                                </Grid>

                            </Grid>
                        </form>
                    </div>
                </Grid>
                <Grid item xs={false} sm={6} md={6} className="image"/>
            </Grid>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        Login: state.Login
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       fill: (text) => {dispatch(fill(text))},
       send: (text) => {dispatch(send(text))}
    }
}
export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Log_in));