import React,{Component}   from 'react';
 


import TextField from '@material-ui/core/TextField';
import confirmEmail from '../../scripts/confirmEmail.js';
 
import {connect} from 'react-redux'
import {change,verify} from '../../_actions/Register'
// view loading
import {openBD} from '../../_actions/actions_backdrop'



class TextEmail extends Component{
    
    constructor(props) {
        super(props)
        this.state = {
            validate: false,
            information: 'Ingresa la información',
        }
    }
    
    inputChange(event){
        
        let id = event.target.id;
        let value = event.target.value;
        
        switch(id){
            case "nombre":
                /*if (value.length < 1){this.setState({validate: true})}
                else{this.setState({validate: false})}*/
                if (value.length < 1){this.props.change(['validate_nombre',true])}
                else{this.props.change(['validate_nombre',false])}
                break
            case "email":
                
                //this.setState({validate: true})
                this.props.change(['validate_email',true])
                let validEmail = confirmEmail(value)
                if (validEmail) {
                    //console.log("VIEW: ",this.props.view)
                    this.props.change(['validate_email',false])
                }
                break
            default:
                break
        }
        this.props.change([id,value])
    }
    /*componentDidUpdate(){
        //this.setState({validate: true})
        const {Register} = this.props
        console.log(Register)
        this.render()
    }*/

    render(){
        const {Register} = this.props
        //console.log("message: ",Register.message)
        let validate = null
        switch(this.props.id){
            case "nombre":
                validate = Register.validate_nombre
                break
            case "email": 
                validate = Register.validate_email
                break
            case "password":
                    validate = Register.validate_password
                break
            case "nit":
                    validate = Register.validate_nit
                break
            default:
                break
        }
        
        return(
            <div>
                <TextField
                    error={validate}
                    variant="outlined"
                    margin="normal"
                    id={this.props.id}
                    required
                    fullWidth
                    label={this.props.label}
                    onChange={this.inputChange.bind(this)}
                    helperText={validate === true ? 
                        Register.message :  [
                            this.props.id === 'email' ? 'Puedes usar letras, numeros y signos de puntuación' : ''
                        ]
                    }
                />
            </div>
        );
    }
}
 

const mapStateToProps = (state) => {
    return {
        Register: state.Register
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       change: (text) => {dispatch(change(text))},
       verify: (text) => {dispatch(verify(text))},
       openbd: (text) => {dispatch(openBD(text))},
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(TextEmail);