import React, { Component } from 'react'


//import AccountCircle from '@material-ui/icons/AccountCircle';
import AppBar from '@material-ui/core/AppBar'
import Button from '@material-ui/core/Button';
//import IconButton from '@material-ui/core/IconButton'
import { Link,withRouter } from 'react-router-dom';
import MenuNav from '../components/MenuNav'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/styles';

import {connect} from 'react-redux'
import {open_menu,change_menu} from '../_actions/actions_navbar'

import '../css/navbar.css'; 

const styles = theme => ({
    grow: {
        flexGrow: 1,
    },
    sectionDesktop: {
        display: 'none',
        alignItems: 'center',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    textButton: {
        color: 'white',
        textDecorationLine: 'none',
    }
});

class Navbar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            
        }
    }
    openMenu(event){
        this.props.open_menu();
        this.props.change_menu(['anchorEl',event.currentTarget])
        
    }

    render() {
        const { classes } = this.props;
        return (
            <div className="grow">
                <AppBar position="static" color = 'primary'>     
                    <Toolbar>
                        <Typography variant="h4" noWrap>IMA</Typography>
                        <div className={classes.grow}/>
                        <div className={classes.sectionDesktop}>
                            <Link className={classes.textButton}  to="/log_in">
                                <Button
                                    fullWidth
                                    color='inherit'
                                    className={classes.submit}
                                    onClick={this.sendReq}>
                                    Ingreso
                                </Button>
                            </Link>
                            <Link  className={classes.textButton} to="/sign_up">
                                <Button
                                    fullWidth
                                    variant='contained'
                                    color="secondary"
                                    className={classes.submit}
                                    onClick={this.sendReq}>
                                    Registro
                                </Button>
                            </Link>
                        </div>
                        <MenuNav/>
                        <div className="section-mobile">

                        </div>
                        
                    </Toolbar>
                    
                </AppBar>
            </div>
            
        );
    }
}

const mapStateToProps = (state) => {
    return {
        
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
        change_menu: (text) => {dispatch(change_menu(text))},
        open_menu: (text) => {dispatch(open_menu(text))},

    }
}

export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(Navbar)))