

const defaultState = {
    // all validate variables must stay in false except rol
    nombre: '',
    validate_nombre: false,
    email: '',
    unique: false,
    validate_email: false,
    password: '',
    validate_password: false,
    // confirm password for the register
    confirm: '',
    validate_confirm: false,
    rol: '',
    validate_rol: true,
    nit: 0,
    // verify the log_in
    authSuccess: false,
    // verify the sign_up
    registerSuccess: false,
    // display message in textField
    message: ''
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE':
            switch(payload[0]){
                case 'nombre':{return {...state,nombre: payload[1]}}
                case 'validate_nombre':{ return{...state,validate_nombre: payload[1]}}
                case 'email':{return {...state,email: payload[1]}}
                case 'unique':{return {...state,unique: payload[1]}}
                case 'validate_email':{ return{...state,validate_email: payload[1]}}
                case 'password':{return {...state,password: payload[1]}}
                case 'validate_password':{ return{...state,validate_password: payload[1]}}
                case 'confirm':{return {...state,confirm: payload[1]}}
                case 'validate_confirm':{ return{...state,validate_confirm: payload[1]}}
                case 'nit': {return{...state,nit: payload[1]}}
                case 'rol':{return{...state,rol: payload[1]}}     
                case 'validate_rol':{ return{...state,validate_rol: payload[1]}}
                case 'authSuccess':{ return{...state,authSuccess: payload[1]}}  
                case 'registerSuccess':{ return{...state,registerSuccess: payload[1]}}  
                case 'message':{ return{...state,message: payload[1]}}  
                default: return state
            }
        default:
            return state
    }
    //return state
}

export default reducer;