import API_ROUTE from '../constants/api_route'
const axios = require("axios")

// open dialog services
export const open = (option) =>{
    return {
        type: 'OPEN',
        payload: option
    }
}

// open notifications dialog 
export const openDN = (option) =>{
    return {
        type: 'OPENDN',
        payload: option
    }
}

// change any value in dialog
export const change = (option) =>{
    return {
        type: 'CHANGE',
        payload: option
    }
}

// Create a service 
export const create = (option) =>{
    return {
        type: 'CREATE',
        payload: option
    }
}

// update a service 
export const update = (option) =>{
    return {
        type: 'UPDATE',
        payload: option
    }
}

// Load all services
