import React,{Component}   from 'react';
 
 
import IconButton from '@material-ui/core/IconButton';
import FormControl from '@material-ui/core/FormControl';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import {testPassword} from '../../scripts/testPassword'
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

import {connect} from 'react-redux'
import {change} from '../../_actions/Register'

 
class TextPassword extends Component{
    constructor(props) {
        super(props)
        this.state = {
            validate: false,
        }
    }

    
    handleClickShowPassword (){
        this.setState({showPassword: !this.state.showPassword });
    };
    handleMouseDownPassword (event){
        event.preventDefault();
    };
    
    async inputChange(event){
        
        let id = event.target.id;
        let value = event.target.value;
        var md5 = require('md5');
        
        if (id === 'password' ){
            if (testPassword(value)){ 
                //this.setState({validate: false})
                this.props.change(['validate_password',false])
            }
            else{this.props.change(['validate_password',true])}
            value = md5(value)
        }

        if (id === 'confirm'){
            //this.setState({validate: true})
            this.props.change(['validate_confirm',true])
            let {Register} = this.props;
            if (Register.confirm.length > 0){
                if(md5(value) === Register.password ){
                    //this.setState({validate: false})
                    this.props.change(['validate_confirm',false])
                }else{
                    //this.setState({validate: true})
                    this.props.change(['validate_confirm',true])
                }
            }
        }
        this.props.change([id,value])
    }
    

    render(){
        const {Register} = this.props
        let validate = null
        switch(this.props.id){
            case "password":
                validate = Register.validate_password
                break
            case "confirm": 
                validate = Register.validate_confirm
                break
        }
        return(
           <div>
                <FormControl variant="outlined" fullWidth={true}>
                    <InputLabel>{this.props.label}</InputLabel>
                    <OutlinedInput
                        //error={this.props.id === 'password' ? this.state.validate : this.state.validateC}
                        error={validate}
                        id={this.props.id}
                        fullWidth={true}
                        type={this.state.showPassword ? 'text' : 'password'}
                        onChange={this.inputChange.bind(this)}
                        endAdornment={
                        <InputAdornment position="end">
                            <IconButton
                                aria-label="toggle password visibility"
                                onClick={this.handleClickShowPassword.bind(this)}
                                onMouseDown={this.handleMouseDownPassword.bind(this)}
                                edge="end"
                                
                                >
                                {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                            </IconButton>
                        </InputAdornment>
                        }
                        labelWidth={this.props.width}
                    />
                </FormControl>
                
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        Register: state.Register
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       change: (text) => {dispatch(change(text))},
    }
}
 
export default connect(mapStateToProps,mapDispatchToProps)(TextPassword);