import React, { Component } from 'react'
import '../css/dialog.css'; 

//import Button from '@material-ui/core/Button';
import CancelIcon from '@material-ui/icons/Cancel';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from '@material-ui/core/Fab';
import { Grid } from '@material-ui/core';

import SendIcon from '@material-ui/icons/Send';
import TextField from '@material-ui/core/TextField';

import {connect} from 'react-redux'
import {changer,openr} from '../_actions/Request'

class DialogRequest extends Component {
    constructor(props){
        super(props);
        this.state = {
            
        }
        
        this.SendRequest = this.SendRequest.bind(this);
        this.changeState = this.changeState.bind(this);
    }

    
    async changeState(event){
        const target = event.target.id;
        const value = event.target.value;
        this.props.changer([target,value])
    }

    async SendRequest(){this.props.openr('close request')}
    render(){
        
        const { request } = this.props;
        

        //const id = this.state.open ? 'simple-popover' : undefined;
        return(
            <Dialog open={request.open}>
                <DialogTitle >
                    <Grid container direction={"row"}>
                    <Grid item xs={10} md= {10} sm={10}>{"Solicitar reserva"}</Grid>
                    <Grid item xs={2} md={2} sm={2} align="right">
                        <CancelIcon onClick={this.SendRequest}/>
                    </Grid>
                    </Grid>
                </DialogTitle>
                <DialogContent>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="title"
                        label="Titulo"
                        autoFocus
                        onChange={this.changeState}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="body"
                        label="Cuerpo"
                        autoFocus
                        onChange={this.changeState}
                    />
                    
                    <DialogActions>
                        <Fab color="secondary" size={'small'} className="fab">
                           <SendIcon onClick={this.SendRequest}/>
                        </Fab>
                    </DialogActions>
                </DialogContent>
            </Dialog>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       request: state.Request
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
       //change: (date) => {dispatch(change(date))},
       openr: (date) => {dispatch(openr(date))},
       changer: (date) => {dispatch(changer(date))},
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(DialogRequest);