import React, { Component } from 'react'
import '../css/user.css';

//import auth from '../router/Auth'
import Card from '@material-ui/core/Card';
//import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import { CssBaseline, Grid} from '@material-ui/core';
import Drawer from '@material-ui/core/Drawer';
import DrawerUser from '../components/DrawerUser';
import ListInfo from '../components/ListInfo'
import Navbar from '../components/NavUser'
import SnackUser from '../components/SnackUser'
import store from '../store'
import Typography from '@material-ui/core/Typography'
import { withRouter } from 'react-router-dom';

import {connect} from 'react-redux'
import * as actions from '../_actions/Drawer';

//var wtf8 = require('wtf-8');

class User extends Component {
    constructor(props) {
        super(props)
        this.state = {
            alter:[{
                origin: 'bogotá',
                destination: 'santa marta',
                duration: '722 km',
                array_id: '1,2',
                array_services: '1,2'
            },
            {
                origin: 'bogotá',
                destination: 'santa marta',
                duration: '722 km',
                array_id: '1,2',
                array_services: '1,2'
            },
            {
                origin: 'bogotá',
                destination:'dorada',
                duration: '104 km',
                array_id: '1',
                array_services: '1'
            }]
        }
    }

    componentDidMount(){ 
        if (localStorage.getItem('view') === 'services'){
            this.props.history.push('/forbidden')
        }
        this.props.loadA('load data')  
    }

    render(){
        //const {drawer} = this.props
        const {dialog} = this.props
        const {register} = this.props

        //let services = dialog.cards
        //let alternatives = drawer.alternatives
        
        //console.log(services)
        //console.log("alñter dbv: ",alternatives)
        let cards = this.state.alter.map(card =>{
            /*console.log(dialog.origin ,"+", card.origin)
            console.log(dialog.destination ,"+", card.destination)*/
            if(dialog.origin === card.origin && dialog.destination === card.destination){
                return(
                    <Card key={card.id}>
                        <CardContent>
                            <Grid container direction="row" >
                                <Grid item xs={9} md={9} align="left">
                                    <Typography gutterBottom color="inherit" component="h2" align="left">
                                    {card.origin+" "}
                                    <ChevronRightIcon  style={{ fontSize: 15 }} />
                                    {" "+card.destination}
                                    </Typography>
                                </Grid>
                                <Grid item xs={3} md={3} align="right">
                                    <Typography gutterBottom  color="primary" component="h4">
                                        15h 30 min
                                    </Typography>
                                </Grid>
    
                                <Grid item xs={8} md={8} align="center">
                                    <p>Información detallada del servicio y
                                    del transportador.
                                    </p>
                                </Grid>
                                <Grid item xs={4} md={4} align="right">{card.duration}</Grid>
                                <Grid item xs={12} md={12}>
                                    <ListInfo type="Detalles" alternative={card}/>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>    
                )   
            }
            else{return(<div></div>)}
        })

        return(
            <div className="root">
                <Grid container component="main" className="pater" direction="row">
                    <Grid item xs={12}>
                        <Navbar/>
                        <CssBaseline/>
                    </Grid>
                    <Grid item xs={12}></Grid>
                        <Drawer
                            className="drawer"
                            variant="persistent"
                            anchor="left"
                            open={store.getState().Drawer.open}
                            classes={{paper: "drawerPaper"}}
                        ><DrawerUser cards={cards}/>
                    </Drawer>
                    <SnackUser message={register.nombre}/>
                </Grid>
                    
            </div>
            
        )
    }
}

const mapStateToProps = (state) => {
    return {
        drawer: state.Drawer,
        dialog: state.Dialog,
        register: state.Register
    };
};

/*const mapDispatchToProps = (dispatch) => {
    return{
       open: (text) => {dispatch(open(text))},
       load: (text) => {dispatch(load(text))}
    }
}*/
export default withRouter(connect(mapStateToProps,actions)(User))