import React, { Component } from 'react'
import '../css/navbar.css'; 

import AppBar from '@material-ui/core/AppBar'
import auth from '../router/Auth'
import AccountCircle from '@material-ui/icons/AccountCircle';
import Badge from '@material-ui/core/Badge';
import CloudQueueIcon from '@material-ui/icons/CloudQueue';
import IconButton from '@material-ui/core/IconButton'
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MyTheme from '../constants/MyTheme';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import {withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/styles';
import {ThemeProvider} from '@material-ui/core/styles';

import {connect} from 'react-redux'
import {open_drawer} from '../_actions/Drawer'



const styles = theme => ({
    grow: {
        flexGrow: 1,
    },
    sectionDesktop: {
        display: 'none',
        alignItems: 'center',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    textButton: {
        color: 'white',
        textDecorationLine: 'none',
    }
});

class Navbar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            openM: false,
            anchor: null
        }
        this.openDrawer = this.openDrawer.bind(this);
        this.openMenu = this.openMenu.bind(this);
        this.logOut = this.logOut.bind(this);
    }

    openDrawer(){
        this.props.open('drawerA')
        //console.log("openDrawer-Admin!")
    }

    openMenu(event){
        this.setState({
            openM: !this.state.openM,
            anchor: event.currentTarget
        })
    }
    // close session
    logOut(){
        auth.logout(() =>{this.props.history.push("/")}) 
        //localStorage.setItem('auth',false)
    }

    render() {
        const menuId = 'primary-search-account-menu';
        

        const { classes } = this.props;
        
        //console.log(Drawer)
        return (
            <div className="grow">
                <ThemeProvider theme={MyTheme}></ThemeProvider>
                <AppBar position="static" style={MyTheme.palette.bprimary}>     
                    <Toolbar>
                        <IconButton
                            edge="start"
                            className={classes.menuButton}
                            color="inherit"
                            aria-label="open drawer"
                            onClick={this.openDrawer}
                        >
                            <MenuIcon />
                        </IconButton>
                        <Typography variant="h4" noWrap>IMA</Typography>
                        <div className={classes.grow}/>
                        <div className={classes.sectionDesktop}>
                            <div className="icon_nav">
                            <IconButton disableRipple={true} aria-label="show 2 new notifications" color="inherit" >
                                <CloudQueueIcon/>
                            </IconButton>
                            
                            <IconButton aria-label="show 2 new notifications" color="inherit" >
                                <Badge badgeContent={1} color="secondary">
                                    <NotificationsIcon />
                                </Badge>
                            </IconButton>
                            </div>
                            <IconButton
                                
                                aria-label="account of current user"
                                color="inherit"
                                onClick={this.openMenu}
                            >
                                <AccountCircle />
                            </IconButton>
                        </div>
                        <div className="section-mobile">
                        </div>
                    </Toolbar>
                </AppBar>

                <Menu
                    anchorEl={this.state.anchor}
                    anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                    id={menuId}
                    keepMounted
                    transformOrigin={{ vertical: 'down', horizontal: 'right' }}
                    open={this.state.openM}
                    onClose={this.openMenu}>
                    <MenuItem onClick={this.LinkProfile}>Perfil</MenuItem>
                    <MenuItem onClick={this.logOut}>Cerrar Sesión</MenuItem>
                </Menu>
            </div>
            
        );
    }
}


const mapStateToProps = (state) => {
    return {
        Drawer: state.Drawer
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       open: (text) => {dispatch(open_drawer(text))},
    }
}
export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(Navbar)));
