const defaultState = {
    users: [],
    email: '',
    password: '',
    token: '',
    rol: '',
    id_user: '',
    isAuth: false
    // response of server
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'FILL':
            switch(payload[0]){
                case 'users':{return {...state,users: payload[1]}}
                case 'email':{return {...state,email: payload[1]}}
                case 'rol':{return {...state,rol: payload[1]}}
                case 'id_user':{return {...state,id_user: payload[1]}}
                case 'token':{return {...state,token: payload[1]}}
                case 'isAuth':{
                    console.log("isAuthQQ!!!_ ",payload[1])
                    return {...state,isAuth: payload[1]}}
                default: return{...state,password: payload[1]}
            }
        default:
            return state
    }
    //return state
}

export default reducer;