import React, { Component } from 'react'
import '../css/home.css';

import Button from '@material-ui/core/Button';
import { CssBaseline, Grid} from '@material-ui/core';
import Hidden from '@material-ui/core/Hidden';
import { Link,withRouter } from 'react-router-dom';
import Navbar from '../components/Navbar'


import service from '../resources/servicios.png';
import equipo from '../resources/equipo.png';
import unal from '../resources/unal.png';
import uncund from '../resources/uc.png';
import sepro from '../resources/sepro.png';



class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            
        }
    }
    render() {
        
        return (
            <div className="root">
                <Grid container direction="row">
                    <Grid item xs={12} >
                        <Navbar/>
                        <CssBaseline/>        
                    </Grid>
                    <Grid container  direction="column"  className="main" >
                        <Hidden only="xs">
                            <Hidden only="sm">
                            <h1 className="main_title">Intermodalidapp</h1>
                            <p className="main_text">Soluciones más allá de las fronteras</p>
                            </Hidden>
                        </Hidden>
                        <Hidden only='md'>
                            <Hidden only='lg'>
                                
                                <Grid container  direction="row">
                                    <Grid item xs={12}>
                                        <h5 className="little_title">Intermodalidapp</h5>
                                    </Grid>    
                                    <Grid item xs={2}></Grid>
                                    <Grid item xs={4}>
                                    <Link className="link" to="/log_in">
                                        <Button
                                            variant='contained'
                                            color="secondary"
                                            onClick={this.sendReq}>
                                            Ingreso
                                        </Button>
                                    </Link>
                                    </Grid>
                                    <Grid item xs={4}>
                                        <Link className="link"  to="/sign_up">
                                            <Button
                                                variant='contained'
                                                color="primary"
                                                onClick={this.sendReq}>
                                                Registro
                                            </Button>
                                        </Link>
                                    </Grid>
                                    <Grid item xs={2}></Grid>
                                </Grid>
                            </Hidden>
                        </Hidden>
                        
                    </Grid>

                    <Grid container  direction="row"   className="div_white">
                        
                        <Grid item xs={12} sm={6} md={6} className="img">
                            <Hidden only='xs'>
                                <img src={service} alt="cu" width="300" height="300"/>
                            </Hidden>
                        </Grid>
                        <Grid item xs={12} sm={6} md={6}>
                            <h3 className="div_title">Servicios</h3>
                            <div className="div_text">
                            Esta aplicacion tiene como finalidad 
                            presentar a los generadores de carga 
                            las diferentes alternativas para el 
                            transporte de su mercancia de manera intermodal.
                            </div>
                        </Grid>
                    </Grid>

                    <Grid container  direction="row"  className="div_black">
                        <Grid item xs={12} sm={6} md={6}>
                            <h3 className="div_title">Equipo</h3>
                            <p className="div_text">
                            Este desarrollo fue realizado por el 
                            grupo de investigación Sepro, un grupo 
                            interdisciplicinar que aborda desde 
                            diferentes puntos de vista los temas 
                            relaciones con la logistica y la cadena de suministro.
                            </p>
                        </Grid>
                        <Grid item xs={12} sm={6} md={6} className="img">
                            <Hidden only='xs'>
                                <img src={equipo} alt="cu" width="300" height="300"/>
                            </Hidden>
                        </Grid>
                    </Grid>

                    
                    <Grid container direction="row" alignItems="center" className="div_white">
                        <Grid item xs={12} sm={6}>
                            <Grid container direction="row">
                                <Grid item xs={12}>
                                <img src={unal} alt="cu" width="160" height="180"/>
                                </Grid>
                            </Grid>
                            <Grid container direction="row">
                                <Grid item xs={6}>
                                    <img src={sepro} alt="sepro" width="150" height="110"/>
                                </Grid>
                                <Grid item xs={6}>
                                    <img src={uncund} alt="cu" width="110" height="120"/>   
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Hidden only='xs'>
                                <h3 className="div_title">Colaboradores</h3>
                                <p className="div_text">
                                En esta etapa del proyecto 
                                se trabajó en convenio de la 
                                Universidad Nacional de Colombia 
                                y la Universidad de Cundinamarca
                                </p>
                            </Hidden>
                            
                        </Grid>

                            
                    </Grid>
                </Grid>
            </div>
            
        );
    }
}
export default withRouter(Home);