import React,{Component}   from 'react';
import '../css/snackbar.css'; 


import Slide from '@material-ui/core/Slide';
import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';
 


class SnackUser extends Component{
    constructor(props) {
        super(props)
        this.state = {
            vertical: 'bottom',
            horizontal: 'left',
            open: true
        }
    }
    
    handleClose(){ this.setState({ open: false })}
    SlideTransition(props) {
        return <Slide {...props} direction="right" />;
    }
    
    render(){
        return(
            <div>
                <Snackbar
                    anchorOrigin={{vertical: this.state.vertical,horizontal: this.state.horizontal}}
                    key={`${this.state.vertical},${this.state.horizontal}`}
                    open={this.state.open}
                    autoHideDuration={4000}
                    TransitionComponent={this.SlideTransition.bind(this)}
                    onClose={this.handleClose.bind(this)}
                >
                    <SnackbarContent style={{ backgroundColor: '#0749e6'}}
                        message={
                            <div className="message_snack">
                                <span id="client-snackbar">Bienvenido</span>
                                <span className="user_snack">{this.props.message}</span>
                            </div>
                            
                        }
                    />
                </Snackbar>
            </div>
        );
    }
}
 
 
export default SnackUser ;