const defaultState = {
    // drawer_user
    open: false ,
    // drawer_admin
    openA: false ,
    setOpen: false,
    alternatives: []
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        
        case 'OPEN_DRAWER':
            switch(payload){
                case 'drawer':
                    //return{...state,open:!state.setOpen,setOpen:!state.open}
                    return{...state,open:!state.open}
                case 'drawerA':
                    return{...state,openA:!state.openA}
                default:
                    return state
            }  
        case 'CHANGE':
            switch(payload[0]){
                case 'alternatives':
                    return{...state,alternatives:payload[1]}
                default:
                    return state
            }    
        default:
            return state
    }
    //return state
}

export default reducer;