import API_ROUTE from '../constants/api_route'
import store from '../store'
const axios = require("axios")



export const change = (option) =>{
    return {
        type: 'CHANGE',
        payload: option
    }
}

export const verify = (option) =>{
    return (dispatch) => {
        let state = store.getState().Register
        //console.log("state: ",state.email)
        axios.post(API_ROUTE, { 
            query : `query{
                userByEmail(email: "${state.email}"){ id }
            }`
        }).then((result) => {
            //console.log(result.data)
            let user = result.data.data.userByEmail
            if (user.id !== -1){dispatch(change(['unique', true]))}
            else{ dispatch(change(['unique', false]))}
        }).catch(err => console.log(err))
    }
}

export const email_success = (validEmail,view) =>{
    
    return (dispatch) => {
        setTimeout(() => { 
            //this.props.verify(validEmail)
            dispatch( verify(validEmail) )
        }, 2000);
        setTimeout(() => {
            let Register = store.getState().Register
            
            // email in the db
            if (Register.unique === true){
                if (view === 'sign_up'){
                    //this.setState({validate: true})
                    //this.props.change(['validate_email',true])
                    //this.setState({information: 'Correo electronico en uso'}) 
                    dispatch(change(['validate_email',true]))  
                    dispatch(change(['message', 'Correo electronico en uso']))
                }
            }
            // email not in the db
            else{
                if (view === 'log_in'){
                    //this.props.change(['validate_email',true])
                    //this.setState({information: 'Correo electrónico no registrado'})
                    dispatch(change(['validate_email',true])) 
                    dispatch(change(['message', 'Correo electrónico no registrado']))
                }
            }
            //console.log("finish email_success",Register.unique)
        }, 3000);
    }
}


// register an user
export const register = () =>{
    //console.log("Register redux")
    let state = store.getState().Register
    //console.log("state-rol: ",state)
    return(dispatch)=> {
        
        //dispatch()
        if (!state.validate_nombre & state.nombre.length > 1 &
            !state.validate_password & state.password.length >= 8 &
            !state.validate_confirm & state.confirm.length >= 8 &
            !state.validate_email  & state.confirm.length >= 6 &
            !state.validate_rol){
                
            axios.post(API_ROUTE, { 
                query : `mutation{
                    createUser(user: {
                    nameE:"${state.nombre}"
                    email:"${state.email}"
                    passw:"${state.password}"
                    rol:"${state.rol}"
                    nit:"${state.nit}"
                    }){
                        nameE
                    }
                }`
                
            }).then((result) => {
                let user = result.data.data.createUser
                //console.log(user.nameE)
                if (user.nameE !== " ") { dispatch(change(['registerSuccess', true]))}
                
            }).catch(err => console.log(err))
        }
        
    } 
}