const defaultState = {
    open: false ,
    setOpen: false,
    option: ''
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE':
            return {...state,option:payload}
        case 'OPEN':
            return {...state,open:!state.setOpen,setOpen:!state.open}
        default:
            return state
    }
    //return state
}

export default reducer;

