import {createStore,combineReducers,applyMiddleware} from 'redux';

import BackDrop from './reducers/BackDrop'
import Dialog from './reducers/Dialog';
import Drawer from './reducers/Drawer'
import ListRol from './reducers/ListRol'
import Login from './reducers/Login'
import Navbar from './reducers/Navbar' 
import Register from './reducers/Register'
import Request from './reducers/Request'
import Snack from './reducers/Snack'
import thunk from 'redux-thunk';
import Train from './reducers/Train'

const reducer = combineReducers({
    BackDrop, Dialog, Drawer, ListRol,  Login, Navbar, Register, Snack, Request, Train
});

const store = createStore(reducer,applyMiddleware(thunk));  
export default store;