import React, { Component } from 'react'
import '../css/register.css';

import BackDrop from '../components/BackDrop'
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Fab from '@material-ui/core/Fab';
import Grid from '@material-ui/core/Grid';
import ListRol from '../components/ListRol'
import Paper from '@material-ui/core/Paper';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import SnackAlert from '../components/_Snacks/SnackAlert';
import TextEmail from '../components/_Form/TextEmail'
//import TextField from '@material-ui/core/TextField';
import TextPassword from '../components/_Form/TextPassword'
import Typography from '@material-ui/core/Typography';
import { withRouter } from 'react-router-dom';


// Redux
import {connect} from 'react-redux'
import {change,register,email_success} from '../_actions/Register'
// view loading
import { openBD } from '../_actions/actions_backdrop' 
import { openSnack } from '../_actions/actions_snack'

class Sign_up extends Component {
    constructor(props) {
        super(props)
        this.state = {
            validate: false,
            success: false,
            view: "password",
            password: '',
            showPassword: false,
            color: '#bf6c2c'
        }
        this.sendRequest = this.sendRequest.bind(this);
    }

    sendRequest(){ 
        
        this.props.openbd('back_drop')
        setTimeout(() => {  
            let { Register } = this.props
            this.props.email_success(Register.email,'sign_up')
        }, 2000);
        
        setTimeout(() => {  
            let { Register } = this.props
            //console.log("before register: ",Register.unique)
            if (!Register.unique){
                setTimeout(() => {  
                    //console.log("start register!!")
                    this.props.register() }, 1000);
                setTimeout(() => {  
                    let { Register } = this.props
                    //console.log("Rsuccess: ",Register.registerSuccess)
                    if (Register.registerSuccess ){ 
                        //console.log("entry true"); 
                        this.changeView()
                    }
                    this.props.openbd('back_drop')
                }, 9000);
            }else{
                this.props.openbd('back_drop');
                this.props.open_snack('snack_alert');
                setTimeout(() => { this.props.open_snack('snack_alert');}, 3000);
            }
        }, 7000);
    }

    changeView(){ this.props.history.push('/log_in')}

    render() {
        
        return (
            <Grid container component="main" className="root">
                <CssBaseline />
                <Grid item xs={false} sm={6} md={6} className="image"/>
                <Grid item xs={12} sm={6} md={6} component={Paper} elevation={5} square>
                    <div className="paper">
                        <div className="Avatar">
                            <Fab color="primary" size={'large'} >
                                <PersonAddIcon/>
                            </Fab>
                        </div>
                        <div className="title">
                            <Typography component="h5" variant="h5" className="title"> Registro</Typography>
                        </div>
                        
                        <form className="form" noValidate>
                            <Grid container item spacing={2} xs={12} justify="center">
                                
                                <BackDrop message="Registrando usuario..."/>
                                <SnackAlert message="Correo electrónico en uso"/>

                                <Grid container direction="row" spacing={1} justify="center">
                                    <Grid item xs={10} sm={9}>
                                        <TextEmail id="nombre" label="Nombre empresa"/>
                                    </Grid>
                                    <Grid item xs={10} sm={9} >
                                        <TextEmail id="email" label="Correo electrónico" view="sign_up"/>
                                    </Grid>
                                </Grid>
                                
                                <Grid item xs={10} sm={9} className="text_password"></Grid>

                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={1} sm={1}></Grid>
                                    <Grid item xs={4} sm={5} className="password" >
                                        <TextPassword label="contraseña" width={78} id="password"/>
                                    </Grid>
                                    <Grid item xs={4} sm={5} className="password" >
                                        <TextPassword label="confirmación" width={88} id="confirm"/>
                                    </Grid>
                                </Grid>

                                <Grid item xs={10} sm={9} className="text_password">
                                    Utiliza ocho caracteres como mínimo con una 
                                    combinación de letras, números y símbolos.
                                </Grid>
                                <Grid item xs={10} sm={5}>
                                    <TextEmail id="nit" label="Nit empresa"/>
                                </Grid>
                                <Grid item xs={10} sm={5} >
                                    <div className="list_signup">
                                        <ListRol />
                                    </div>
                                </Grid>
                                
                                <Grid container spacing={3} direction="row" justify="center" className="submit_signup">
                                    <Grid item xs={6} sm={4} className="submit">
                                        <Button
                                            fullWidth
                                            variant="contained"
                                            color="primary"
                                            className="submit"
                                            onClick={this.sendRequest}>
                                            Registrar usuario
                                        </Button>
                                    </Grid>

                                    <Grid item xs={6} sm={6} className="submit">
                                        <Button
                                            fullWidth
                                            variant="outlined"
                                            color="secondary"
                                            className="submit"
                                            onClick={this.changeView.bind(this)}>
                                            prefiero iniciar sesión
                                        </Button>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </form>
                    </div>
                </Grid>
            </Grid>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        Register: state.Register
    };
};
 
const mapDispatchToProps = (dispatch) => {
    return{
       change: (text) => {dispatch(change(text))},
       // Register an user
       register: (text) => {dispatch(register(text))},
       email_success: (text) => {dispatch(email_success(text))},
       openbd: (text) => {dispatch(openBD(text))},
       open_snack: (text) => {dispatch(openSnack(text))},
    }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Sign_up));