import React, { Component } from 'react'
import '../css/panel.css'; 

import Button from '@material-ui/core/Button';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Grid from '@material-ui/core/Grid';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';


var wtf8 = require('wtf-8');
class Panel extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            open: false
        }
        this.handleChange = this.handleChange.bind(this)
    }
    handleChange(){this.setState({open: !this.state.open})}
    

    render(){
        //console.log("this.props in panel: ",this.props.cardReq)
        return(
            <div className="wrap">
                <List className="list">
                    <ListItem> 
                        <ExpansionPanel expanded={this.state.open} onChange={this.handleChange}>
                            <ExpansionPanelSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls="panel1bh-content"
                                id="panel1bh-header"
                                >
                                <Typography className="heading">{wtf8.decode(this.props.cardReq.title)}</Typography>
                                <Typography className="secondaryHeading">{this.props.cardReq.accept}</Typography>
                                </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                            
                            <Grid container direction={"row"}>
                                <Grid item xs={12} md={12} sm={12}>
                                    <Typography>
                                        Cada petición presentada por el usuario tiene un cuerpo, el cual 
                                        presenta de manera resumida la intención de la misma, en esta ocasión es
                                        presentado a continuación:<br/><br/>
                                        {wtf8.decode(this.props.cardReq.body)}
                                    </Typography>
                                </Grid>

                                <Grid item xs={4} md={8} sm={4} className="item"></Grid>
                                <Grid item xs={4} md={2} sm={4} className="item" align="right">
                                    <Button variant='contained' color="secondary">Aceptar</Button>
                                </Grid>
                                <Grid item xs={4} md={2} sm={4} className="item" align="right">
                                    <Button variant="outlined" color="secondary">Rechazar</Button>
                                </Grid>
                            </Grid>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>    
                    </ListItem>
                </List>
            </div>
        )
    }
}

export default Panel;