import React,{Component}   from 'react';
 
 
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem'; 
import {withRouter } from 'react-router-dom';

import {connect} from 'react-redux'
import {open_menu} from '../_actions/actions_navbar'

class MenuNav extends Component{
    constructor(props) {
        super(props)
        this.state = {}
    }
    handleClose(){
        this.props.open_menu()
    }

    logOut(){
        this.props.open_menu()
        setTimeout(() => { 
            //this.props.verify(validEmail)
            localStorage.setItem('auth',false)    
            this.props.history.push("/") 
        }, 2000);
    }

    returnView(){
        this.props.open_menu()
        if (localStorage.getItem('view') === 'user'){
            this.props.history.push("/user")
        }else{
            this.props.history.push("/services")
        }
    }

    render(){
        const menuId = 'primary-search-account-menu';
        const {Navbar} = this.props
        return(
            <div>
                <Menu
                    id={menuId}
                    anchorEl={Navbar.anchorEl}
                    keepMounted
                    open={Navbar.open}
                    onClose={this.handleClose.bind(this)}>
                    <MenuItem onClick={this.returnView.bind(this)}>Panel</MenuItem>
                    <MenuItem onClick={this.handleClose.bind(this)}>Administrar tu cuenta</MenuItem>
                    <MenuItem onClick={this.logOut.bind(this)}>Cerrar Sesión</MenuItem>
                </Menu>
            </div>
        );
    }
}
 
const mapStateToProps = (state) => {
    return {
        Navbar: state.Navbar
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
        open_menu: (text) => {dispatch(open_menu(text))},
    }
}

export default withRouter((connect(mapStateToProps,mapDispatchToProps)(MenuNav)));