import React, { Component } from 'react'
import '../css/dialogN.css'; 

import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import { CssBaseline, Grid} from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import IconButton from '@material-ui/core/IconButton';
/*import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';*/
import Panel from './Panel'
import Slide from '@material-ui/core/Slide';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';


import {connect} from 'react-redux'
import {openDN} from '../_actions/Dialog'
import {loadr} from '../_actions/Request'

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

class DialogNotification extends Component {
    constructor(props){
        super(props);
        this.state = {
            openP: false,
            anchorEl: null
        }
        
        //this.handleClick = this.handleClick.bind(this)
        this.handleClose = this.handleClose.bind(this)
    }
    componentDidMount(){this.props.loadr('load requests')}
    handleClose(){
        this.props.openDN('dialogN')
    }
    render(){
        const { dialog } = this.props;
        const { request } = this.props;
        //console.log("request_ :",request['requests'])
        let requestCard = request['requests'].map(card=>{
            return(
                <Panel key={card.id} cardReq={card}/>
            )
        })
        
        return(
            <Dialog fullScreen open={dialog.openN} onClose={this.handleClose} TransitionComponent={Transition}>
                <div className="root-not">
                <Grid container direction="row">
                    <Grid item xs={12}>
                        <AppBar className="appBar">
                            <Toolbar>
                                <IconButton edge="start" color="inherit" onClick={this.handleClose} aria-label="close">
                                    <CloseIcon />
                                </IconButton>
                                <Typography variant="h6" className="title"></Typography>
                                <Button 
                                    variant="contained" 
                                    color="secondary" 
                                    onClick={this.handleClose}>
                                    save
                                </Button>
                            </Toolbar>
                        </AppBar>
                        <CssBaseline/> 
                    </Grid>
                    <Grid container  direction="column" >
                        {requestCard}                            
                    </Grid>
                </Grid>
                </div>
                
            </Dialog>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       dialog: state.Dialog,
       request: state.Request,
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
       openDN: (date) => {dispatch(openDN(date))},
       // load requests
       loadr: (date) => {dispatch(loadr(date))}
    }
}


export default connect(mapStateToProps,mapDispatchToProps)(DialogNotification);