import React,{Component}   from 'react';
import '../../css/snackbar.css'; 


import Slide from '@material-ui/core/Slide';
import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';


import {connect} from 'react-redux'
import { openSnack } from '../../_actions/actions_snack'

class SnackAlert extends Component{
    constructor(props) {
        super(props)
        this.state = {
            open: true
        }
    }
    
    SlideTransition(props) {
        return <Slide {...props} direction="right" />;
    }

    render(){
        const { Snack } = this.props;
        return(
            <div>
                <Snackbar
                    key={`${this.state.vertical},${this.state.horizontal}`}
                    open={Snack.open}
                    autoHideDuration={3000}
                    TransitionComponent={this.SlideTransition.bind(this)}
                    //onClose={()=>{this.props.open_snack('snack_alert')}}
                >
                    <SnackbarContent style={{ backgroundColor: '#c9432e'}}
                        message={
                            <div className="message_snackAlert">
                                {this.props.message}
                            </div>
                        }
                    />
                </Snackbar>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        Snack: state.Snack
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
        open_snack: (text) => {dispatch(openSnack(text))},
    }
}

 
export default connect(mapStateToProps,mapDispatchToProps)(SnackAlert);