import React, { Component } from 'react'
import '../css/cities.css'; 

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import { RadioGroup, Collapse } from '@material-ui/core';

import {connect} from 'react-redux'
import {change} from '../_actions/Dialog'

class ListTrans extends Component {
    constructor(props){
        super(props);
        this.state = {
            open: false,
            setOpen: false
        }
        this.OpenList = this.OpenList.bind(this);
        this.ChangeOption = this.ChangeOption.bind(this);

    }
    OpenList(){
        this.setState({open: !this.state.setOpen, setOpen: !this.state.open})
    }

    async ChangeOption(event){
        let option = event.target.value
        this.props.change([this.props.type,option]);
    }
    
    render(){
        
        //console.log("cities: ",ListTrans)
        return(
            <List justify="center">
                <ListItem button onClick={this.OpenList}>
                <ListItemText primary={this.props.type}/>
                    {this.state.open ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Collapse in={this.state.open} timeout="auto" unmountOnExit>
                    <RadioGroup>
                        <ListItem >
                            <FormControlLabel control={ <Radio className="list" onChange={this.ChangeOption} value="terrestre" name={"algo"}/>}
                            label="Terrestre"/>
                        </ListItem>
                        <ListItem >
                            <FormControlLabel control={ <Radio className="list" onChange={this.ChangeOption} value="ferreo"/>}
                            label="Ferreo"/>
                        </ListItem>    
                        <ListItem >
                            <FormControlLabel control={ <Radio className="list" onChange={this.ChangeOption} value="fluvial"/>}
                            label="Fluvial"/>
                        </ListItem>
                        <ListItem >
                            <FormControlLabel control={<Radio className="list" onChange={this.ChangeOption} value="aereo"/>}
                            label="Aereo"/>
                        </ListItem>
                    </RadioGroup>
                </Collapse>
            </List>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        dialog: state.Dialog,
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        change: (value) => {dispatch(change(value))},
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(ListTrans);